import m from "mithril";

export default {
  oninit({ state, attrs }) {
    state.internalName = `wizard.${attrs.name}`;

    if (attrs.value) {
      localStorage.setItem(state.internalName, attrs.value);
      return;
    }

    state.value = localStorage.getItem(state.internalName)
      ? localStorage.getItem(state.internalName)
      : "";
  },
  view({ attrs: { type = "checkbox", name, onchange, oninput }, state }) {
    return m("input[class='']", {
      type,
      name,
      "aria-describedby": state.name,
      checked: state.value === "true",
      onchange: m.withAttr("checked", v => {
        state.value = `${v}`;
        localStorage.setItem(state.internalName, v);
      })
    });
  }
};
