import m from "mithril";

export default {
  oninit({ state, attrs }) {
    state.internalName = `wizard.${attrs.name}`;

    // if (window.env === "production")
    //   if (attrs.type === "password") {
    //     return;
    //   }

    if (attrs.value) {
      localStorage.setItem(state.internalName, attrs.value);
      return;
    }

    state.value = localStorage.getItem(state.internalName)
      ? localStorage.getItem(state.internalName)
      : "";
  },
  view({
    attrs: { name, onchange, oninput, options, valueKey, labelKey },
    state
  }) {
    return m(
      "select.form-control",
      {
        name,
        id: name,
        onchange() {
          var selectBox = document.getElementById(name);
          var selectedValue = selectBox.options[selectBox.selectedIndex].value;
          state.value = selectedValue;
          localStorage.setItem(state.internalName, state.value);
        }
      },
      [
        m("option[value='']", "Select"),
        options.map(option => {
          return m(
            "option",
            {
              selected: state.value === option[valueKey],
              value: option[valueKey]
            },
            option[labelKey]
          );
        })
      ]
    );
  }
};
