import m from "mithril";

export default {
  oninit({ state, attrs }) {
    state.internalName = `wizard.${attrs.name}`;

    state.value = localStorage.getItem(state.internalName)
      ? localStorage.getItem(state.internalName)
      : undefined;
  },
  view({ attrs: { type = "radio", value, name, onchange, oninput }, state }) {
    return m("input[class='']", {
      type,
      name,
      value,
      checked: state.value === value,
      onclick: m.withAttr("checked", v => {
        state.value = value;
        localStorage.setItem(state.internalName, value);
      })
    });
  }
};
