import m from "mithril";

export default {
  oninit({ state, attrs }) {
    state.internalName = `wizard.${attrs.name}`;

    if (window.env === "production")
      if (attrs.type === "password") {
        return;
      }

    if (attrs.value) {
      localStorage.setItem(state.internalName, attrs.value);
      return;
    }

    state.value = localStorage.getItem(state.internalName)
      ? localStorage.getItem(state.internalName)
      : "";
  },
  view({ attrs: { type = "text", name, onchange, oninput }, state }) {
    return m("input.form-control[autocomplete='off']", {
      type,
      name,
      value: state.value,
      oninput: m.withAttr("value", v => {
        state.value = v;
        localStorage.setItem(state.internalName, v);
      })
    });
  }
};
